

body {
    background-image: linear-gradient(to right,#080808,#101010);
}

a{
    color : #00ff41;
    text-decoration: none;
    cursor: pointer;
}


.experience-title {
    text-align: center;
    margin-top: 3em;
    font-size: 1.5rem;
    font-family: 'Share Tech Mono';
    color : #00ff41;
}

.exp-container {
    margin-top: 1.0em;
    justify-content: center;
    margin-left: 0.6em;
    margin-right: 0.6em;
    margin-bottom: 0.5em;
}

.exp-content {
    margin-top : 0.8em;
    margin-bottom : 0.8em;
    margin-left : 0.5em;
    margin-right: 0.5em;
    padding : 0.4em;
    color : #00ff41;
    font-size: 1.1em;
    font-family: 'Share Tech Mono';
    border : 1px dashed white;
    overflow-wrap: break-word;
    line-height: 1.4;
    letter-spacing: 0.1em;
}

.exp-title {
    margin : 0.2em;
    font-size: 1.4em;
    font-family: 'Mode Seven';
}

.exp-desc {
    margin: 0.4em;
    animation: fadein 2s 1;
}

.exp-icon{
    margin-right:0.2em;
    margin-left: 0.1em;
}

.exp-time {
    margin-left: 0.5em;
}

.exp-icon-cal{
    margin-left: 0.3em;
    
}

@keyframes fadein{
    0% {opacity: 0;}
    49%{  opacity: 0;  }
    60%{ opacity: 0.3; }
    70%{ opacity: 0.5; }
    80%{opacity: 0.8;  }
    100% {opacity: 1;}
}


@media (min-height : 811px ) {
    

    .exp-container {
        margin-top: 1.0em;
        justify-content: center;
        margin-left: 0.6em;
        margin-right: 0.6em;
        margin-bottom: 0.5em;
    }

    .exp-icon{
        margin-right:0.2em;
        margin-left: 0.1em;
    }
    .exp-time {
        margin-left: 0.5em;
    }

    .exp-icon-cal{
        margin-left: 0.3em;
        
    }

}


@media (max-width : 320px) {

    .experience-title {
        text-align: left;
        margin-top: 4em;
        margin-left:1em;
        font-size: 1.5rem;
        font-family: 'Share Tech Mono';
        color : #00ff41;
    }

    .exp-container {
        margin-top: 1.0em;
        margin-left: 0.6em;
        margin-right: 0.6em;
        
        justify-content: center;
        margin-bottom: 0.5em;
    }

    .exp-icon{
        margin-right:0.2em;
        margin-left: 0.1em;
    }
    .exp-time {
        margin-left: 0.5em;
    }

    .exp-icon-cal{
        margin-left: 0.3em;
        
    }
}


@media (min-device-width : 768px)  {
    

    .e-div {
        display: flex;
        justify-content: center;
    }

    .exp-container{
        width : 200vh;
    }

    .experience-title {
        text-align: left;
        margin-top: 2.5em;
        margin-left:1.7em;
        font-size: 2.0rem;
        font-family: 'Share Tech Mono';
        color : #00ff41;
    }

    .exp-content {
        margin : 0.8em;
        padding : 0.8em;
        color : #00ff41;
        font-size: 1.1em;
        border : 1px dashed white;
        font-family: 'Share Tech Mono';
        overflow-wrap: break-word;
        line-height: 1.4;
        letter-spacing: 0.1em;
    }
    
    .exp-time {
        margin-left: 0.5em;
    }

    .exp-icon{
        margin-right:0.2em;
        margin-left: 0.1em;
    }

    .exp-icon-cal{
        margin-left: 0.3em;
        
    }
    
    
}